.coin-container {
  display: flex;
  justify-content: center;
}
.coin-row {
  padding: 20px;
  display: flex;
  flex-direction: row;
  justify-content: start;
  align-items: center;
  height: 60px;
  border-bottom: 1px solid #d7d7d7;
  width: 1000px;
}

.coin {
  display: flex;
  align-items: center;
  padding-right: 24px;
  min-width: 300px;
}

.coin h1 {
  font-size: 16px;
  width: 150px;
}

.coin img {
  height: 30px;
  width: 30px;
  margin-right: 10px;
}

.coin-symbol {
  text-transform: uppercase;
}

.coin-data {
  display: flex;
  text-align: center;
  justify-content: space-between;
  width: 100%;
}

.coin-price {
  width: 110px;
}

.coin-volume {
  width: 200px;
}

.coin-marketcap {
  width: 240px;
}

.coin-percent {
  width: 80px;
}

.red {
  color: #f00606;
}

.green {
  color: #00a008;
}

@media screen and (max-width: 800px) {
  .coin-row {
    height: 120px;
    width: 400px;
  }

  .coin {
    display: block;
    padding: 10px;
    min-width: 100px;
  }

  .coin h1 {
    font-size: 14px;
    width: 70px;
  }

  .coin img {
    height: 30px;
    width: 30px;
  }

  .coin-symbol {
    font-size: 10px;
  }

  .coin-data {
    display: block;
    padding: 10px;
    width: 100%;
  }

  .coin-price {
    font-size: 14px;
    width: 0px;
    padding-bottom: 5px;
  }

  .coin-volume {
    width: 0px;
    font-size: 10px;
    padding-bottom: 5px;
  }

  .coin-marketcap {
    width: 100px;
    text-align: left;
    font-size: 10px;
  }

  .coin-percent {
    width: 0px;
    font-size: 12px;
    padding-bottom: 5px;
  }
}

